.hero {
  width: 100%;
  height: 74vh;
  position: relative;
}

.hero-mid {
  width: 100%;
  height: 60vh;
  position: relative;
  background-position: top right;
}

.hero-low {
  width: 100%;
  height: 50vh;
  position: relative;
  background-position: top right;
}

img {
  width: 100%;
  height: 120%;
  object-fit: cover;
  vertical-align: middle;
  margin: 0;
  left: 0;
}

.hero-text {
  position: absolute; /* Position the text absolutely within the container */
  top: 55%; /* Adjust the top position as needed */
  left: 50%; /* Adjust the left position as needed */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}

.hero-text h1 {
  background: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: justify;
  padding: 10% 40% 0px 15%;
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 1.1;
  font-family: Inter, sans-serif;
}

.hero-text p {
  font-size: 1.3rem;
  font-weight: 500;
  color: #fff;
  padding:
    0.5re,
    0 2re,
    0;
}

@media screen and (max-width: 555px) {
  .hero-text h1 {
    padding: 10px 20px;
  }

  .hero-text p {
    font-size: 1.1rem;
    padding: 0 0 2rem 0;
  }
}

.NavbarItems {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  box-shadow: 0 5px 15px 0 rgb(0, 0, 0, 0.25);
  width: 70%;
  height: 100px;
  /* border-radius: 1px; */
  position: fixed;
  top: 30px;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  z-index: 999;
}

.navbar-logo {
  color: #222;
  font-size: 2rem;
  cursor: pointer;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  list-style: none;
  align-items: center;
}

.nav-links {
  text-decoration: none;
  color: #222;
  font-size: 1.2rem;
  font-weight: 500;
  padding: 0.7rem 1rem;
}

.nav-links:hover {
  background-color: rgb(228, 5, 5);
  color: #fff;
  border-radius: 4px;
  transition:
    all,
    0.2s ease-in-out;
}

.home-link {
  text-decoration: none;
  color: #222;
}

.menu-icons {
  display: none;
}

@media screen and (max-width: 850px) {
  .NavbarItems {
    z-index: 99;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background: #fff;
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: -100%;
    opacity: 0;
    align-items: stretch;
    padding: 90px 0 30px 0;
    margin: 0;
  }

  .nav-menu.active {
    left: 0;
    opacity: 1;
    z-index: -1;
    transition: 0.3s ease-in-out;
  }

  .nav-links {
    display: block;
    width: 100%;
    font-size: 1.2rem;
    padding: 2rem 0;
  }

  .nav-links:hover {
    background-color: rgb(228, 5, 5);
    transition: none;
  }


  .menu-icons {
    display: block;
    cursor: pointer;
  }

  .menu-icons i {
    font-size: 1.2rem;
    color: #222;
  }
}

