.header-img-wrapper {
  width: 100%;
  max-height: none;
  overflow: hidden;
  /* border-radius: 7px; */
  float: right;
  padding-top: 0;
}

.header-img {
  /* width: 200px; */
  height: 500px;
  overflow: hidden;
  /* border-radius: 7px; */
  float: right;
}

.header-2col-img-bg {
  grid-auto-columns: 1fr;
  display: grid;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  grid-template-rows: auto;
  grid-template-columns: 2fr 1fr;
}

.page-padding {
  padding-left: 5%;
  padding-right: 5%;
}

.section-header49 {
  -webkit-text-size-adjust: 100%;
  color: #000;
  display: block;
  background-color: #f4f4f4;
  position: relative;
  height: 400px;
  /* height: 410px; */
}

.padding-vertical.padding-xhuge.nav-margin.lateral-pad {
  /* padding: 7rem; */
  padding-top: 15rem;
  padding-left: 14rem;
}

/* h1 {
  text-align: padding-left;
} */

.img {
  border: 0;
  vertical-align: middle;
  max-width: 100%;
  display: inline-block;
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.publication {
  grid-template-rows: auto;
  grid-template-columns: 1fr;
  margin-bottom: 0;
  display: block;
}

.cursive-text {
  font-family: "Poppins", sans-serif;
  font-style: italic;
}

.paper-details {
  border-top: 2px solid rgba(25, 30, 32, 0.05);
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  /* padding-left: 2rem; */
  opacity: 1;
  box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.1);
  /* width: 70%; */
  text-align: justify;
}

.text-size-regular {
  padding-top: 1rem;
  text-align: left;
}

.publication-list {
  padding-top: 5rem;
  padding-bottom: 2rem;
  /* padding-left: 2rem; */
  width: 90%;
  margin-left: 15%;
}

.reference-link {
  background-color: transparent;
  border-color: #000;
  font-weight: 500;
  color: inherit;
  text-decoration: inherit;
  font-size: inherit;
  max-width: 80%;
  display: inline-block;
  width: 100%;
}

.heading-small {
  /* font-size: 1.5rem; */
  /* font-weight: 600; */
  line-height: 1.4;
  color: #000;
}
.max-width-medium {
  width: 100%;
  /* max-width: 55rem; */
}

.year-heading {
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.4;
  color: #000;
  padding-top: 5%;
}
/* .formatted-reference{
  cursor: pointer;
} */

.pu-heading {
  text-align: center;
  padding-top: 10%;
}

.text-size-medium {
  text-align: center;
  /* padding-top: 10%; */
}

@media screen and (max-width: 850px) {
  .pu-heading {
    text-align: center;
    padding-top: 50%;
  }

  .text-size-medium {
    text-align: center;
    /* padding-top: 10%; */
  }
}
